import React from 'react'
import { Row, Col, Form } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb } from '@/hooks/useApp'
import { useReplaceUrl } from '@/hooks/useLink'
import { useReportTemplateList } from '@/hooks/useReportTemplate'
import * as routes from '@/constants/routes'
import Select from '@/components/select'
import Input from '@/components/input'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import Table from '@/components/query-table'
import { ReportTemplate } from '@/services/reportService'

export default function ReportInquiry() {
  useTitle('輸入報告')
  useBreadcrumb([{ name: '輸入報告' }, { name: '輸入報告查詢' }])

  const [form] = Form.useForm()

  const [dataSource] = useReportTemplateList()

  const handleTo = useReplaceUrl()

  const columns: ColumnProps<ReportTemplate>[] = [
    {
      title: '報告名稱',
      dataIndex: 'name',
      render: (v, r) => (
        <Button
          data-id={r.id}
          type="link"
          onClick={handleTo(routes.REPORT_INPUT)}>
          {v}
        </Button>
      ),
    },
    {
      title: '創作者',
      dataIndex: 'author',
    },
    {
      title: '狀態',
      dataIndex: 'statusName',
    },
    {
      title: '創作時間',
      dataIndex: 'createdAtStr',
    },
    {
      title: '上次修改時間',
      dataIndex: 'updatedAtStr',
    },
    {
      title: '報告描述',
      dataIndex: 'description',
    },
  ]

  return (
    <Page>
      <Form
        layout="horizontal"
        form={form}
        hideRequiredMark={true}
        colon={false}>
        <div className="bg-white p-12 mb-8 rounded-lg shadow-lg">
          <Row gutter={24}>
            <Col span={8}>
              <Form.Item label="審核醫師" name="doctorId">
                <Select allowClear placeholder="選擇醫師" className="w-2/3">
                  <Select.Option value={1}>醫師名1</Select.Option>
                  <Select.Option value={2}>醫師名2</Select.Option>
                  <Select.Option value={3}>醫師名3</Select.Option>
                </Select>
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="申請序號" name="serialNo">
                <Input className="w-2/3" placeholder="輸入申請序號" />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="報告格式">
                <Input className="w-2/3" placeholder="輸入報告格式" />
              </Form.Item>
            </Col>
          </Row>
        </div>
        <Row>
          <Col span={24}>
            <Table rowKey="id" columns={columns} dataSource={dataSource} />
          </Col>
        </Row>
      </Form>
    </Page>
  )
}
